import { Component } from '@angular/core';
import { BottomsheetComponent } from './components/bottomsheet/bottomsheet.component';
import { Title } from "@angular/platform-browser";
import { MatBottomSheet, MatBottomSheetRef } from '@angular/material/bottom-sheet';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  title = 'Jasper Baes';
  selectedTab = -1;
  navLinks: any[];
  openPost = null;

  constructor(private router: Router, private _bottomSheet: MatBottomSheet, private titleService: Title) {
    this.titleService.setTitle("Jasper Baes");
    this.navLinks = [
      {
        label: 'About me',
        link: './',
        index: 0
      // }, {
      //   label: 'Posts',
      //   link: './posts',
      //   index: 1
      }, {
        label: 'Timeline',
        link: './timeline',
        index: 1
      },
      {
        label: 'CAPS tool',
        link: './caps',
        index: 2
      }
    ];
  }

  onTabClick(event) {
    // this.router.navigate(['/', event.tab.textLabel.toLowerCase()]).then();
    if (event.tab.textLabel.toLowerCase() == 'posts') {
      this.router.navigate(['/posts']);
    } else if (event.tab.textLabel.toLowerCase() == 'timeline') {
      this.openPost = null;
      this.router.navigate(['/timeline']);
    } else if (event.tab.textLabel.toLowerCase() == 'caps tool') {
      this.openPost = null;
      this.router.navigate(['/caps']);
    } else {
      this.router.navigate(['/']);
    }
  }

  ngOnInit(): void {
    this.router.events.subscribe((res) => {
      this.selectedTab = this.navLinks.indexOf(this.navLinks.find(tab => tab.link === '.' + this.router.url));
    });
  }

  openBottomSheet() {
    this._bottomSheet.open(BottomsheetComponent);
  }

  receivePost($event) {
    this.openPost = $event
    this.selectedTab = 1;
    this.router.navigate(['/posts']);
  }

  receiveTab($event) {
    this.selectedTab = $event;
    if ($event == '0') {
      this.openPost = null;
    }
  }


  // SWIPE

  private swipeCoord?: [number, number];
  private swipeTime?: number;

  swipe(e: TouchEvent, when: string): void {
    const coord: [number, number] = [e.changedTouches[0].clientX, e.changedTouches[0].clientY];
    const time = new Date().getTime();

    if (when === 'start') {
      this.swipeCoord = coord;
      this.swipeTime = time;
    } else if (when === 'end') {
      const direction = [coord[0] - this.swipeCoord[0], coord[1] - this.swipeCoord[1]];
      const duration = time - this.swipeTime;

      if (duration < 1000 //
        && Math.abs(direction[0]) > 30 // Long enough
        && Math.abs(direction[0]) > Math.abs(direction[1] * 3)) { // Horizontal enough
        const swipe = direction[0] < 0 ? 'next' : 'previous';
        console.log('swipe', direction[0] < 0 ? 'next' : 'previous')

        if (swipe == 'next') {
          if (this.selectedTab < 3) this.selectedTab++;
        } else {
          if (this.selectedTab > 0) this.selectedTab--;
        }
      }
    }
  }
}

